export default{
	name:'Dialog',
	props:{
		visible:{
			type:Boolean,
			default:false
		},
		title:{
			type:String,
			default:'标题'				
		},
		cancelBtnTxt:{
			type:String,
			default:'取消'
		},
		affirmBtnTxt:{
			type:String,
			default:'确认'
		},
		btnLocation:{
			type:String,
			default:'right'
		},
		opacity:{
			type:Number,
			default:0.6
		}
	},
	computed:{
		getBtnLocation(){
			return this.btnLocation == 'left'?'flex-start': this.btnLocation == 'right'?'flex-end':'center'
		}
	},
	methods:{
		onCancel(){
			this.$emit('update:visible', false)
			this.$emit('cancel')
		},
		onAffirm(){
			this.$emit('affirm')
		}
	}
}
